import { all } from 'redux-saga/effects';
import { authSaga } from './authSaga';
import { categorySaga, blogCategoryWatcher } from './categorySaga';
import { blogWatcher, blogSaga, blogDetailWatcher } from './blogSaga';
import { permissionSaga } from './permissionSaga';
import { roleSaga } from './roleSaga';
export default function* rootSaga() {
    yield all([
        authSaga(),
        categorySaga(),
        blogCategoryWatcher(),
        blogSaga(),
        blogWatcher(),
        blogDetailWatcher(),
        roleSaga(),
        permissionSaga()
    ]);
}