import {
    SET_ROLES,
    ROLES_LOADING,
    SET_ROLE,
    REMOVE_ROLE_COMPLETE
} from "../actions/types";

const initialState = {
    roles: [],
    loading: false
};

export default function (state = initialState, action) {
    switch (action.type) {
        case ROLES_LOADING:
            return {
                ...state,
                loading: !state.loading
            }
        case SET_ROLES:
            return {
                ...state,
                roles: action.payload,
                loading: false
            }
        case SET_ROLE:
            return {
                ...state,
                role: action.payload,
                loading: false
            }
        case REMOVE_ROLE_COMPLETE:
            return {
                ...state,
                loading: false,
                roles: state.roles.filter(role => role.id !== action.payload.id)
            }
        default:
            return state;
    }
}