import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { createRole } from '../../actions/roleActions';
import { getPermissions } from '../../actions/permissionActions';
import Breadcrumb from '../main/Breadcrumb';
import { Link } from 'react-router-dom';
import isEmpty from '../../utils/isEmpty';
import InputForm from '../form/InputForm';
class RoleForm extends Component {
    constructor() {
        super();
        this.state = {
            name: '',
            permissions: [],
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onStatus() {
        this.setState({
            status: !this.state.status
        })
    }
    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }
    componentDidMount() {
        this.props.getPermissions();
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
    }
    onClickChoose = (event) => {
        const idx = this.state.permissions.indexOf(event.currentTarget.dataset.id);
        if (idx > -1) {
            this.setState({
                permissions: this.state.permissions.filter(item => item !== event.currentTarget.dataset.id)
            })
        }
        else {
            this.setState({
                permissions: [...this.state.permissions, event.currentTarget.dataset.id]
            })
        };
    }
    onSubmit(e) {
        e.preventDefault();
        const roleData = {
            data: {
                name: this.state.name
            },
            permissions: this.state.permissions
        }
        this.props.createRole(roleData);
    }
    render() {
        const { errors } = this.state;
        const { permissions } = this.props.permissions;
        const { loading } = this.props.roles;
        let rolesLoading;
        if (loading) {
            rolesLoading = <h1>Loading</h1>
        } else {
            rolesLoading = (
                <div>
                    <button className="btn btn-info" type="submit">
                        <i className="ace-icon fa fa-check bigger-110"></i>
                        Thm mi
                    </button>
                    <Link to="/roles" className="btn btn-warning" role="button">
                        <i className="ace-icon fa fa-close bigger-110"></i>
                        Hy
                    </Link>
                </div>
            )
        }
        return (
            <div className="main-content-inner">
                <Breadcrumb title="Add role" link="/roles" previous="Role" />
                <div className="page-content">
                    <div className="page-header">
                        <h1>Thm mi Role</h1>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <form className="form-horizontal" onSubmit={this.onSubmit}>
                                <div className="row">
                                    <div className="col-sm-8">
                                        <InputForm
                                            name="name"
                                            placeholder="Name"
                                            value={this.state.name}
                                            onChange={this.onChange}
                                            error={errors.name}
                                            title="Name"
                                        />
                                        <div className="widget-box widget-color-blue2">
                                            <div className="widget-header">
                                                <h4 className="widget-title lighter smaller">Chn quyn</h4>
                                            </div>
                                            <div className="widget-body">
                                                <div className="widget-main padding-8">
                                                    {!isEmpty(permissions) ? (
                                                        <ul className="tree tree-unselectable">
                                                            {
                                                                permissions.map(category => {
                                                                    return (
                                                                        <li className="tree-branch tree-open" key={category.id}>
                                                                            <div className="tree-branch-header">
                                                                                <span className="tree-branch-name">
                                                                                    <i className="icon-folder ace-icon tree-minus"></i>
                                                                                    <span className="tree-label">{category.name}</span>
                                                                                </span>
                                                                            </div>
                                                                            <ul className="tree-branch-children">
                                                                                {category.permission.map(permission => (
                                                                                    <li className="tree-item" key={permission.id}>
                                                                                        <input type="checkbox" onClick={this.onClickChoose} data-id={permission.id} />
                                                                                        <span className="tree-label">{permission.title}</span>
                                                                                    </li>
                                                                                ))}
                                                                            </ul>
                                                                        </li>
                                                                    )
                                                                })
                                                            }
                                                        </ul>
                                                    ) : null}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="clearfix form-actions">
                                    <div className="col-md-offset-3 col-md-9">
                                        {rolesLoading}
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div >
            </div >
        )
    }
}
RoleForm.propTypes = {
    errors: PropTypes.object.isRequired,
    getPermissions: PropTypes.func.isRequired,
    permissions: PropTypes.object.isRequired,
    roles: PropTypes.object.isRequired,
}

const mapStateToProps = (state) => ({
    permissions: state.permissions,
    roles: state.roles,
    errors: state.errors,
});
export default connect(mapStateToProps, { getPermissions, createRole })(RoleForm);
