import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { removeRole } from '../../actions/roleActions';
class RoleItem extends Component {
    constructor() {
        super();
        this.state = {

        };
        this.onDeleteItem = this.onDeleteItem.bind(this);
    }
    onDeleteItem(id) {
        if (window.confirm('Are you sure you wish to delete this item?')) {
            this.props.removeRole(id);
        }
    }
    render() {
        const { role } = this.props;
        return (
            <tr>
                <td>{role.name}</td>
                <td>
                    <div className="btn-group">
                        <Link to={{ pathname: `/roles/${role.id}` }} className="btn btn-xs btn-info">
                            <i className="ace-icon fa fa-pencil bigger-120"></i>
                        </Link>

                        <button className="btn btn-xs btn-danger" onClick={() => this.onDeleteItem(role.id)}>
                            <i className="ace-icon fa fa-trash-o bigger-120"></i>
                        </button>
                    </div>
                </td>
            </tr>
        )
    }
}
RoleItem.propTypes = {
    role: PropTypes.object.isRequired,
    removeRole: PropTypes.func.isRequired
}

const mapStateToProps = (state) => ({

});
export default connect(mapStateToProps, { removeRole })(RoleItem);
