import { all, call, put, takeLatest } from 'redux-saga/effects';
import RequestService from '../utils/requestService';
import Message from '../utils/notificationMessage';
import history from '../utils/history';
import * as types from '../actions/types';

function* setRolesLoading() {
    yield put({ type: types.ROLES_LOADING });
}

function* fetchRoles() {
    try {
        const result = yield RequestService.get('/api/roles');
        yield put({ type: types.SET_ROLES, payload: result.data.data });
    } catch (error) {
        console.log(error.response);
    }
}

function* fetchRole(action) {
    const { id } = action;
    try {
        const result = yield RequestService.get(`/api/roles/${id}`);
        yield put({ type: types.SET_ROLE, payload: result.data.data });
    } catch (error) {
        console.log(error.response);
    }
}

function* clearError() {
    yield put({ type: types.CLEAR_ERRORS })
}

function* createRole(data) {
    yield call(clearError);
    yield call(setRolesLoading);
    try {
        const result = yield RequestService.post('/api/roles', data.data);
        Message.displaySuccess(result.data.message);
        yield call(history.push, '/roles');
    } catch (error) {
        yield call(setRolesLoading);
        if (error.response.data.message) {
            Message.displayError(error.response.data.message);
            if (error.response.data.data) {
                yield put({ type: types.GET_ERRORS, payload: error.response.data.data });
            }
        }
    }
}

function* updateRole(data) {
    yield call(clearError);
    yield call(setRolesLoading);
    try {
        const result = yield RequestService.put('/api/roles/' + data.id, data.data);
        Message.displaySuccess(result.data.message);
        yield call(history.push, '/roles');
    } catch (error) {
        yield call(setRolesLoading);
        if (error.response.data.message) {
            Message.displayError(error.response.data.message);
            if (error.response.data.data) {
                yield put({ type: types.GET_ERRORS, payload: error.response.data.data });
            }
        }
    }
}

function* removeRole(data) {
    try {
        const result = yield RequestService.del('/api/roles/' + data.id);
        Message.displaySuccess(result.data.message);
        yield put({ type: types.REMOVE_ROLE_COMPLETE, payload: result.data.data });
    } catch (error) {
        if (error.response.data.message) {
            Message.displayError(error.response.data.message);
        }
    }
}

export function* roleSaga() {
    yield all([
        takeLatest(types.CREATE_ROLE, createRole),
        takeLatest(types.REMOVE_ROLE, removeRole),
        takeLatest(types.UPDATE_ROLE, updateRole),
        takeLatest(types.GET_ROLES, fetchRoles),
        takeLatest(types.GET_ROLE, fetchRole)
    ])
}