import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Breadcrumb from '../main/Breadcrumb';
import { getRoles } from '../../actions/roleActions';
import RoleFeed from './RoleFeed';
import { Link } from 'react-router-dom';

class Role extends Component {
    componentDidMount() {
        this.props.getRoles();
    }
    render() {
        const { roles } = this.props.roles;
        return (
            <div>
                <div className="main-content-inner">
                    <div className="breadcrumbs ace-save-state" id="breadcrumbs">
                        <Breadcrumb name="Role" />
                    </div>
                    <div className="page-content">
                        <div className="widget-box">
                            <div className="widget-header">
                                <h4 className="widget-title">Qun l role</h4>
                                <div className="widget-toolbar no-border">
                                    <Link to="/roles/add" className="btn btn-sm btn-info">
                                        <i className="ace-icon fa fa-plus"></i> Thm mi role
                                    </Link>
                                </div>
                            </div>
                            <div className="widget-body">
                                <div className="widget-main">
                                    <table id="simple-table" className="table table-bordered table-hover">
                                        <thead>
                                            <tr>
                                                <th width="70%">Name</th>
                                                <th width="30%">Custom</th>
                                            </tr>
                                        </thead>

                                        <tbody>
                                            {roles ? <RoleFeed roles={roles} /> : null}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
Role.propTypes = {
    getRoles: PropTypes.func.isRequired,
    roles: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    roles: state.roles
});
export default connect(mapStateToProps, { getRoles })(Role);
